*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace;
  color: #eeeeee;
  background-color: #212121;
  background-image: url(/smart_brain/static/media/bg-image.fe10683d.jpg);
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  box-sizing: border-box;
}

button {
  cursor: pointer;
}

.home {
  min-height: 100vh;
  display: flex;
  flex-flow: column;
  justify-content: center;
}

a {
  color: inherit;
  outline: none;
}

.App {
  text-align: center;
  position: relative;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}

.spinner {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2000;
  background-color: rgba(255,255,255,.3);
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #fff transparent #fff transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.lds-roller {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-roller div {
  -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  -webkit-transform-origin: 32px 32px;
          transform-origin: 32px 32px;
}
.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #fff;
  margin: -3px 0 0 -3px;
}
.lds-roller div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 50px;
  left: 50px;
}
.lds-roller div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 54px;
  left: 45px;
}
.lds-roller div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 57px;
  left: 39px;
}
.lds-roller div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 58px;
  left: 32px;
}
.lds-roller div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
  top: 57px;
  left: 25px;
}
.lds-roller div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
  top: 54px;
  left: 19px;
}
.lds-roller div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
  top: 50px;
  left: 14px;
}
.lds-roller div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
  top: 45px;
  left: 10px;
}
@-webkit-keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navigation {
  display: flex;
  justify-content: flex-end;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 50;
}

.navigation-container {
  margin: 10px;
  background-image: -webkit-linear-gradient(254.5deg, #eeeeee 0%, #eeeeee 50%, #424242 50%);
  height: 70px;
  display: flex;
  align-items: center;
}

.navigation-link {
  padding: 5px 10px;
  font-size: 25px;
  text-decoration: none;
}

.navigation-link:hover {
  color: #9e9e9e;
}

.active {
  text-decoration: underline;
}

.signout-link {
  margin: 20px;
}

.signin-link {
  background-color: #424242;
}

.register-link {
  background-color: #eee;
  color: #424242;
}

.Tilt {
  background-color: #eeeeee;
}

.imageLinkForm-container {
}

.imageLinkForm {
  width: 75%;
  height: 48px;
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  justify-content: center;
  position: relative;
  margin-top: 48px;
  background-color: #fff;
  border-radius: 4px;
  border: 2px solid #1b2634;
}

.imageLinkForm-input {
  width: 100%;
  border-radius: 4px;
  box-sizing: content-box;
  box-shadow: none;
  background-color: transparent;
  color: #1c1e1e;
  border: none;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  padding: 12px 85px 12px 5px;
  height: 100%;
  caret-color: #1b2634;
  -webkit-transition: border-color .2s;
  transition: border-color .2s;
}

.imageLinkForm-button {
  position: absolute;
  right: 6px;
  background-color: #009cff;
  width: 77px;
  height: 36px;
  border-radius: 2px;
  text-transform: uppercase;
  color: #fff;
  font-size: 14px;
  padding: 11px 0;
  border: none;
  /* opacity: 0; */
  -webkit-transition: opacity .2s,visibility 0s .2s;
  transition: opacity .2s,visibility 0s .2s;
  /* visibility: hidden; */
}

.imageLinkForm-button:focus {
  outline: none;
}

.bounding_box {
  position: absolute;
  box-shadow:  0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}
.input-container {
  width: 90%;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  margin: 8px 0;
}

.form-input {
  width: 100%;
  padding: 13px;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #eeeeee;
  color: #212121;
  border: none;
  border-radius: 3px;
  box-sizing: border-box;
  -webkit-transition: all .2s;
  transition: all .2s;
  border-bottom: 3px solid transparent;
}

.form-input:focus {
  outline: none;
  box-shadow: 0 5px 20px rgba(0,0,0,0.2);
}

.input-label {
  margin-left: 13px;
  margin-top: 10px;
  font-family: Arial, Helvetica, sans-serif;
  color: #bdbdbd;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: all .2s;
  transition: all .2s;
}

.form-input:placeholder-shown + .input-label {
  -webkit-transform: translateY(-44px);
          transform: translateY(-44px);
  opacity: 0;
  visibility: hidden;
}

.form-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form {
  background-color: #fff;
  background-image: -webkit-linear-gradient(146deg, #1e1e1e 0%, #1e1e1e 65%, #373737 65%);
  background-image: linear-gradient(-56deg, #1e1e1e 0%, #1e1e1e 65%, #373737 65%);
  border: 1px solid #424242;
  background-size: cover;
  width: 350px;
  border-radius: 4px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  padding: 142px 20px 20px 20px;
}

.form-header {
  margin: 0;
  width: 100%;
  background-image: -webkit-linear-gradient(146deg, #1e1e1e 0%, #1e1e1e 65%, #373737 65%);
  background-image: linear-gradient(-56deg, #1e1e1e 0%, #1e1e1e 65%, #373737 65%);
  background-size: cover;
  border: 1px solid #424242;
  box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
  color: #fff;
  text-align: center;
  padding: 16px;
  border-radius: 4px;
  display: block;
  margin-top: -180px;
  margin-bottom: 20px;
  right: 16px;
  left: 16px;
}

.form-heading {
  margin: 16px;
  font-size: 28px;
  line-height: 1.2;
  font-family: inherit;
  font-weight: 100;
  font-size: 35px;
  box-sizing: border-box;
}

.form-button {
  cursor: pointer;
  box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
  padding: 13.44px 34.24px;
  font-size: 12.96px;
  border: 0;
  border-radius: 2px;
  text-transform: uppercase;
  white-space: normal;
  word-wrap: break-word;
  display: inline-block;
  font-weight: 400;
  text-align: center;
  line-height: 1.5;
  font-family: inherit;
  box-sizing: border-box;
  vertical-align: middle;
  background-color: #0099cc;
  -webkit-transition: all .2s;
  transition: all .2s;
}

.form-button:disabled {
  background-color: #fff;
  cursor: not-allowed;
}

.form-button:hover {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.form-button:focus {
  outline: none;
}

